@import url(https://fonts.googleapis.com/css?family=Gothic+A1:300,300i,400,400i,500,500i,600,600i,700,700i|Martel:300,300i,400,400i,700,700i,900,900i|Work+Sans:600,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*-----------------------------------------------------------
        Loading
------------------------------------------------------------*/
.siteLoading{
  background-color: #030303;
  width: 100%;
  height: 100%;
  position: fixed;
  opacity: 1;
  pointer-events: none;
  transition: opacity cubic-bezier(.4, 0, .2, 1) 436ms;
  z-index: 9999;
}

.siteLoading-frame {
  display: -moz-flex;
  display: flex;
  -moz-flex-direction: column;
  flex-direction: column;
  -moz-justify-content: center;
  justify-content: center;
  -webkit-animation: fadein 436ms;
  animation: fadein 436ms;
  height: 98%;
  }

.siteLoading-image{
  width: 112px;
  height: 112px;
  align-self: center;
}



/* ===================================================================
 * # hero
 *
 * ------------------------------------------------------------------- */
 .s-hero {
    width: 100%;
    /* height: 100vh;
    min-height: 82rem; */
    overflow: hidden;
    background-color: transparent;
    position: relative;
  }
  
  .s-hero::before {
    display: block;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, black 10%, rgba(0, 0, 0, 0) 100%);
    opacity: .25;
  }
  
  .no-js .s-hero {
    background: #ffffff;
  }
  
  .hero-content {
    height: 100%;
    max-width: 1400px;
    align-items: flex-end;
    padding-top: 20vh;
    padding-bottom: 14vh;
    position: relative;
  }
  
  .hero-content h1 {
    font-size: 7rem;
    font-weight: 400;
    line-height: 1.257;
    padding-right: 3.2rem;
    margin-top: 0;
    color: #ffffff;
    position: relative;
  }
  
  .hero-content h1::before {
    content: "";
    display: block;
    height: 2px;
    width: 8rem;
    background-color: #03E39B;
    position: absolute;
    left: -10.8rem;
    top: .55em;
  }
  
  /* -------------------------------------------------------------------
   * ## hero social
   * ------------------------------------------------------------------- */
  .hero-social {
    list-style: none;
    display: flex;
    flex-flow: row wrap;
    /* font-family: "Gothic A1", sans-serif; */
    font-size: 2rem;
    /* font-weight: 700; */
    line-height: 3.2rem;
    letter-spacing: .5em; 
    margin-top: 8rem;
    margin-left: .4rem;
    margin-bottom: 0;
    position: relative;
  }
  
  .hero-social a {
    color: #ffffff;
  }
  
  .hero-social a:hover,
  .hero-social a:focus,
  .hero-social a:active {
    color: #03E39B;
  }
  
  .hero-social li {
    padding-left: 0;
  }
  
  .hero-social li::after {
    content: '/';
    color: rgba(255, 255, 255, 0.1);
    margin-left: 0rem;
    margin-right: 1rem;
  }
  
  .hero-social li:last-child::after {
    display: none;
  }
  
  /* -------------------------------------------------------------------
   * ## hero scroll
   * ------------------------------------------------------------------- */
  .hero-scroll {
    position: absolute;
    right: 8rem;
    bottom: 10rem;
  }
  
  .hero-scroll::after {
    content: "";
    display: block;
    width: 2px;
    height: 7rem;
    background-color: white;
    transform: translate3d(50%, 0, 0);
    position: absolute;
    right: 50%;
    bottom: -10rem;
  }
  
  .hero-scroll .scroll-link,
  .hero-scroll .scroll-link svg path {
    transition: all .3s ease-in-out;
  }
  
  .hero-scroll .scroll-link {
    text-decoration: none;
    border: 0 none;
    display: block;
    height: 6.4rem;
    width: 6.4rem;
    border-radius: 50%;
    background-color: rgba(50, 50, 50, 0.5);
    margin: 0;
    position: relative;
  }
  
  .hero-scroll .scroll-link svg {
    height: 1.2rem;
    width: 1.2rem;
    position: absolute;
    transform: translate3d(-50%, -50%, 0);
    left: 50%;
    top: 50%;
  }
  
  .hero-scroll .scroll-link svg path {
    fill: #ffffff;
  }
  
  .hero-scroll .scroll-link:hover,
  .hero-scroll .scroll-link:focus {
    background-color: #03E39B;
  }
  

  
/* ------------------------------------------------------------------- 
 * responsive:
 * hero
 * ------------------------------------------------------------------- */
@media screen and (max-width:1800px) {
    .hero-content h1 {
      font-size: 6.3rem;
    }
  
    .hero-content h1::before {
      width: 6rem;
      left: -8.8rem;
    }
  
  }
  
  @media screen and (max-width:1700px) {
    .hero-content {
      max-width: 1200px;
    }
  
  }
  
  @media screen and (max-width:1500px) {
    .hero-content {
      max-width: 1000px;
    }
  
  }
  
  @media screen and (max-width:1300px) {
    .hero-content {
      max-width: 1200px;
    }
  
    .hero-content h1 {
      font-size: 6rem;
      padding-top: 3.2rem;
    }
  
    .hero-content h1::before {
      width: 6rem;
      top: 0;
      left: 0;
    }
  
  }
  
  @media screen and (max-width:1200px) {
    .hero-scroll {
      right: 6rem;
    }
  
    .hero-scroll::after {
      height: 8rem;
    }
  
    .hero-scroll .scroll-link {
      height: 6rem;
      width: 6rem;
    }
  
  }
  
  @media screen and (max-width:1000px) {
    .hero-content h1 {
      font-size: 5.6rem;
    }
  
    .hero-social {
      font-size: 2rem;
    }
  
  }
  
  @media screen and (max-width:800px) {
    .hero-content h1 {
      font-size: 4.8rem;
    }
  
    .hero-social {
      padding-right: 7.2rem;
    }
  
    .hero-scroll {
      right: 3.6rem;
    }
  
    .hero-scroll .scroll-link {
      height: 5.2rem;
      width: 5.2rem;
    }
  
  }
  
  @media screen and (max-width:600px) {
    .hero-content h1 {
      font-size: 4.6rem;
    }
  
    .hero-content h1 br {
      display: none;
    }
  
    .hero-social {
      /* font-size: 1rem; */
    }
  
    .hero-scroll {
      right: 2.4rem;
    }
  
  }
  
  @media screen and (max-width:500px) {
    .hero-content h1 {
      font-size: 4.4rem;
      padding-right: 0;
    }
  
  }
  
  @media screen and (max-width:400px) {
    .hero-content h1 {
      font-size: 4rem;
    }
  
  }
  
  @media screen and (max-width:360px) {
    .hero-content h1 {
      font-size: 3.8rem;
    }
  
  }
  


/* ===================================================================
 * # about section
 *
 * ------------------------------------------------------------------- */
 .s-about {
    padding-bottom: 2rem;
    background-color: #ffffff;
    position: relative;
  }

  .s-about .section-intro {
    margin-bottom: 3em;
  }
  
  .s-about .right-vert-line {
    background-color: #302c59;
  }
  
  .s-about__section--profile {
    padding-top: 28rem;
    padding-bottom: 4rem;
    min-height: 108rem;
    position: relative;
  }
  
  .s-about__section--profile::before {
    content: "";
    display: block;
    background-color: #b3c2c1;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: calc(50% + 12vw);
  }
  
  .s-about__section--profile .row {
    position: relative;
  }
  
  .s-about__section--profile .profile-pic {
    width: 24vw;
    border-radius: 25%;
    position: absolute;
    top: 6rem;
    left: calc(50% + 80px);
  }
  .s-about__section--profile .profile-pic img {
    border-radius: 25%;
  }
  
  

/* ------------------------------------------------------------------- 
 * ## career position
 * ------------------------------------------------------------------- */
.work-positions {
  margin-top: 2rem;
}

.work-positions .column:nth-child(2n + 1) {
  padding-right: 40px;
}

.work-positions .column:nth-child(2n + 2) {
  padding-left: 40px;
}

.position {
  padding-top: 3.6rem;
  padding-bottom: 3.2rem;
  border-top: 1px solid #b3c2c1;
}

.position__header {
  margin-bottom: 4rem;
}

.position__header h6 {
  font-family: "Martel", serif;
  line-height: 2.8rem;
  margin-top: 0;
  margin-bottom: .4rem;
}

.position__header h6 .position__co {
  margin-right: 2rem;
}

.position__header h6 .position__pos {
  display: inline-block;
  font-size: 1.8rem;
  text-transform: none;
  letter-spacing: 0;
}

.position__timeframe {
  font-family: "Gothic A1", sans-serif;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 2rem;
  text-transform: uppercase;
  letter-spacing: .3rem;
  color: #7e7e7e;
}

/* ------------------------------------------------------------------- 
 * responsive:
 * about
 * ------------------------------------------------------------------- */
@media screen and (max-width:1200px) {
  .s-about__section--profile::before {
    left: 75%;
  }

  .s-about__section--profile .profile-pic {
    width: 26vw;
    right: 0;
    left: auto;
  }

  .work-positions .column:nth-child(2n + 1) {
    padding-right: 30px;
  }

  .work-positions .column:nth-child(2n + 2) {
    padding-left: 30px;
  }

}

@media screen and (max-width:900px) {
  .work-positions .column:nth-child(n) {
    padding: 0 16px;
  }

  .position {
    padding-right: 8rem;
  }

}

@media screen and (max-width:800px) {
  .s-about__section--profile {
    padding-top: 15rem;
    padding-bottom: 0;
    min-height: 0;
  }

  .s-about__section--profile::before {
    display: none;
  }
  .s-about__section--profile .profile-pic {
    position: relative;
    margin: 0 auto;
    padding-bottom: 3em;
    min-width: 200px;

  }

  .position {
    padding-right: 0;
  }

}

@media screen and (max-width:600px) {
  .work-positions .column:nth-child(n) {
    padding: 0 10px;
  }

}

@media screen and (max-width:400px) {
  .work-positions .column:nth-child(n) {
    padding: 0;
  }

}


/* ===================================================================
 * # skills section
 *
 * ------------------------------------------------------------------- */
 .s-skills {
    padding-top: 24rem;
    padding-bottom: 20rem;
    background-color: #1d1b37;
    position: relative;
  }
  
  .s-skills__content {
    position: relative;
  }
  
  .s-skills__leftcol {
    padding-bottom: 12rem;
  }
  
  
  .s-skills .vert-line {
    width: 2px;
    height: 20rem;
    background-color: #03E39B;
    transform: translate3d(50%, 0, 0);
    position: absolute;
    bottom: -20rem;
    left: 20px;
  }
  
  /* ------------------------------------------------------------------- 
   * ## skills list
   * ------------------------------------------------------------------- */
  .skills-list {
    list-style: disc;
    text-align: center;
    padding-left: 20px;
    margin: 8.8rem 0 0 0;
    align-items: center;
  }
  
  .skills-list h5 {
    color: #ffffff;
  }
  
  .skills-list li:nth-child(even):before, .skills-list li:nth-child(even):after {
    content: '\25cf';
    color: #03E39B;
    
  }


  .skills-list li:nth-child(even):before {
    margin-right: 0.5em;
  }
  .skills-list li:nth-child(even):after { 
    margin-left: 0.5em;
  }


  .skills-list li { 
    color: #ffffff;
    font-size: 1.25em;
    font-weight: 700;
    white-space: nowrap;
    display: inline-flex;
    margin:0.5em;
    transition: all .2s ease-in-out;
  }

  .skills-list__item-header {
    padding-top: 2.4rem;
    padding-bottom: 2.4rem;
    padding-right: 6rem;
    cursor: pointer;
    position: relative;
  }
  
  /* ------------------------------------------------------------------- 
   * responsive:
   * skills
   * ------------------------------------------------------------------- */
  @media screen and (max-width:1200px) {
    .skills-list {
      padding-left: 10px;
    }
  }
  
  @media screen and (max-width:1040px) {
    .s-skills .vert-line {
      height: 12rem;
      left: 16px;
    }
  
    .s-skills__content .column {
      flex: 0 0 100%;
      max-width: 100%;
    }
  
    .s-skills__content .s-skills__leftcol {
      flex: 0 0 75%;
      max-width: 75%;
      padding-bottom: 0;
    }
  
  
    .skills-list {
      padding-left: 0;
      margin: 0;
    }
  
  }
  
  @media screen and (max-width:800px) {
    .s-skills {
      padding-top: 20rem;
    }
  
    .s-skills__content .s-skills__leftcol {
      flex: 0 0 100%;
      max-width: 100%;
    }
  
  }
  
  @media screen and (max-width:600px) {
    .s-skills .vert-line {
      left: 34px;
    }

  }
  
  @media screen and (max-width:500px) {

  }
  
  @media screen and (max-width:400px) {
    .s-skills .vert-line {
      left: 24px;
    }
  
  
  }
  
  
/* ===================================================================
 * # portfolio
 *
 * ------------------------------------------------------------------- */
.s-portfolio {
  padding-top: 24rem;
  padding-bottom: 0;
  background-color: #ffffff;
  position: relative;
}

.s-portfolio::before {
  content: "";
  display: block;
  background-color: #b3c2c1;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 50%;
  z-index: 1;
}

.project-wrapper {
  min-width: 225px;
}

.portfolio-item {
  padding: 25px;
  cursor: pointer;
}

#portfolio>.s-portfolio__list {
  border-top: 1px solid #b3c2c1;
}


.portfolio-item-app {
  width: 20em;
  max-width: 200px;
  border-radius: 25%;
  margin-right: 25px;
}

.portfolio-item-website {
  /* height: 300px; */
  min-height: 300px;
  border-radius: 5%;
}

.portfolio-item-app img {
  border-radius: 25%;
}

.portfolio-item-website img {
  border-radius: 5%;
}

/* .portfolio-item img {
    border-radius: 25%;
  } */

.portfolio-item .item-wrap {
  background: #fff;
  overflow: hidden;
  position: relative;
  border-radius: 5%;

}

.portfolio-item .item-wrap a {
  display: block;
  cursor: pointer;
}

/* overlay */
.portfolio-item .item-wrap .overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 5%;

  opacity: 0;
  -moz-opacity: 0;
  filter: alpha(opacity=0);

  background-color: rgba(44, 44, 44, 0.7);
}

.portfolio-item-app .item-wrap .overlay {
  border-radius: 25%;
}

.portfolio-item .item-wrap .link-icon {
  display: block;
  color: #fff;
  height: 30px;
  width: 30px;
  font-size: 18px;
  line-height: 30px;
  text-align: center;

  opacity: 0;
  -moz-opacity: 0;
  filter: alpha(opacity=0);
  transition: opacity 0.3s ease-in-out;

  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -15px;
  margin-top: -15px;
}

.portfolio-item .item-wrap img {
  vertical-align: bottom;
}

.portfolio-item .portfolio-item-meta {
  padding: 18px
}

.portfolio-item .portfolio-item-meta h5 {
  margin-top: 0;
  font: 14px/21px 'opensans-bold', sans-serif;
  color: #fff;
}

.portfolio-item .portfolio-item-meta p {
  font: 12px/18px 'opensans-light', sans-serif;
  color: #c6c7c7;
  margin-bottom: 0;
}

/* on hover */
.portfolio-item:hover .overlay {
  opacity: 1;
  -moz-opacity: 1;
  filter: alpha(opacity=100);
}

.portfolio-item:hover .link-icon {
  opacity: 1;
  -moz-opacity: 1;
  filter: alpha(opacity=100);
}






/* ------------------------------------------------------------------- 
   * ## portfolio list
   * ------------------------------------------------------------------- */
.s-portfolio__list {
  max-width: 1240px;
}


/* ------------------------------------------------------------------- 
   * responsive:
   * portfolio
   * ------------------------------------------------------------------- */
@media screen and (max-width:1200px) {
  .s-portfolio::before {
    display: none;
  }

  .portfolio-item-app {
    /* width: 15vw; */
    margin: 0 auto;
  }

}

@media screen and (max-width:800px) {
  .s-portfolio {
    padding-top: 20rem;
  }

  .portfolio-item {
    /* width: 15vw; */
    padding-bottom: 3em;
    min-width: 200px;
  }


}

/* ===================================================================
 * # footer
 *
 * ------------------------------------------------------------------- */
 .s-footer {
    padding-top: 24rem;
    padding-bottom: 8rem;
    background-color: #0b0a15;
    position: relative;
  }
  
  .s-footer .right-vert-line {
    background-color: #03E39B;
  }
  
  .s-footer__bottom {
    font-family: "Gothic A1", sans-serif;
    color: rgba(255, 255, 255, 0.5);
    margin-top: 8rem;
  }
  
  .s-footer .footer-email-us .btn:hover,
  .s-footer .footer-email-us .btn:focus {
    background-color: #ffffff;
    border-color: #ffffff;
    color: #000000;
  }
  
  .s-footer .footer-contacts {
    margin-top: 7.2rem;
    margin-left: 8.33333%;
  }
  
  /* ------------------------------------------------------------------- 
   * ## contact block
   * ------------------------------------------------------------------- */
  .footer-contact-block {
    font-family: "Gothic A1", sans-serif;
    font-weight: 400;
    font-size: 3rem;
    line-height: 1.2;
  }
  
  .footer-contact-block a {
    color: #ffffff;
  }
  
  .footer-contact-block a:hover,
  .footer-contact-block a:focus {
    color: #03E39B;
  }
  
  .footer-contact-block__content,
  .footer-contact-block__list li {
    padding: .8rem 0;
  }
  
  .footer-contact-block__list {
    list-style: none;
    margin-top: .4rem;
    margin-left: 0;
  }
  
  .footer-contact-block__list li {
    padding-left: 0;
  }
  
  .footer-contact-block__header {
    color: rgba(255, 255, 255, 0.4);
    margin: 0;
  }
  
  /* ------------------------------------------------------------------- 
   * ## copyright
   * ------------------------------------------------------------------- */
  .ss-copyright {
    z-index: 2;
    position: relative;
  }

  .ss-copyright:hover {
    visibility: visible;
  }
  
  .ss-copyright a {
    color: #ffffff;
  }
  
  .ss-copyright a:hover,
  .ss-copyright a:focus {
    
    color: #03E39B;
  }
  
  .ss-copyright span {
    font-size: 1.5rem;
    display: inline-block;
  }
  
  .ss-copyright span::after {
    content: "|";
    display: inline-block;
    padding: 0 .8rem 0 1rem;
    color: rgba(255, 255, 255, 0.1);
  }
  
  .ss-copyright span:last-child::after {
    display: none;
  }
  
  /* ------------------------------------------------------------------- 
   * ## go top
   * ------------------------------------------------------------------- */
  .ss-go-top {
    z-index: 2;
    opacity: 0;
    visibility: hidden;
    transform: translate3d(0, 200%, 0);
    transition: all 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
    position: fixed;
    bottom: 8.4rem;
    right: 8rem;
  }
  
  .ss-go-top a {
    text-decoration: none;
    border: 0 none;
    display: block;
    height: 6.4rem;
    width: 6.4rem;
    border-radius: 50%;
    background-color: #000000;
    transition: all .3s;
    position: relative;
  }
  
  .ss-go-top a:hover,
  .ss-go-top a:focus {
    background-color: #03E39B;
  }
  
  .ss-go-top svg {
    height: 1.2rem;
    width: 1.2rem;
    position: absolute;
    transform: translate3d(-50%, -50%, 0);
    left: 50%;
    top: 50%;
  }
  
  .ss-go-top svg path {
    fill: #ffffff;
  }
  
  .ss-go-top.link-is-visible {
    opacity: 1;
    visibility: visible;
    transform: translate3d(0, 0, 0);
  }
  
  /* ------------------------------------------------------------------- 
   * responsive:
   * footer
   * ------------------------------------------------------------------- */
  @media screen and (max-width:1200px) {
    .ss-go-top {
      right: 6rem;
    }
  
    .ss-go-top a {
      height: 6rem;
      width: 6rem;
    }
  
  }
  
  @media screen and (max-width:900px) {
    .s-footer .footer-contacts {
      margin-left: 0;
    }
  
    .s-footer__main .column {
      flex: 0 0 100%;
      max-width: 100%;
    }
  
  }
  
  @media screen and (max-width:800px) {
    .s-footer {
      padding-top: 20rem;
    }
  
    .ss-copyright {
      padding-right: 10rem;
    }
  
    .ss-go-top {
      right: 3.6rem;
    }
  
    .ss-go-top a {
      height: 5.2rem;
      width: 5.2rem;
    }
  
  }
  
  @media screen and (max-width:600px) {
    .ss-copyright {
      padding-right: 8rem;
    }
  
    .ss-copyright span {
      display: block;
    }
  
    .ss-copyright span::after {
      display: none;
    }
  
    .ss-go-top {
      right: 2.4rem;
      bottom: 4rem;
    }
  
  }
  
