 *
 * ------------------------------------------------------------------- */
.s-header {
  z-index: 100;
  width: 100%;
  height: 8.8rem;
  position: absolute;
  top: 4rem;
  left: 0;
}

.s-header.offset {
  transform: translate3d(0, -100%, 0);
  transition: all .5s;
}

.s-header.scrolling {
  transform: translate3d(0, 0, 0);
}

.s-header.sticky {
  background-color: #050505;
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
}

.s-header.sticky .header-logo img {
  width: 35px;
  height: 38px;
}

.s-header.sticky .header-content .btn {
  height: 4.8rem !important;
  line-height: 4.4rem !important;
}

.s-header.sticky .header-nav a {
  color: rgba(255, 255, 255, 0.6);
}

.s-header.sticky.scrolling {
  opacity: 1;
  visibility: visible;
}

.header-content {
  height: 8.8rem;
  position: relative;
}

/* -------------------------------------------------------------------
 * ## header logo
 * ------------------------------------------------------------------- */
.header-logo {
  z-index: 101;
  display: inline-block;
  margin: 0;
  padding: 0px;
  transition: all .5s;
  transform: translate3d(0, -50%, 0);
  position: absolute;
  left: 8rem;
  top: 50%;
  
}
.logo-border { 
  flex: 1 1;
  padding: 5px;
  background-color: #fff;
  border-radius: 25%;
}

.header-logo a {
  display: block;
  border: none;
  padding: 0;
  outline: 0;
}

.header-logo img {
  width: 41px;
  height: 44px;
  margin: 0 auto;
  vertical-align: bottom;
}

/* -------------------------------------------------------------------
 * ## main navigation
 * ------------------------------------------------------------------- */
.header-nav-wrap {
  max-width: 1400px;
  padding-left: 10px;
}

.header-nav {
  list-style: none;
  font-family: "Gothic A1", sans-serif;
  font-weight: 700;
  font-size: 1.4rem;
  line-height: 6rem;
  padding-top: 1.4rem;
  padding-bottom: 1.4rem;
  margin: 0;
}

.header-nav a {
  color: rgba(255, 255, 255, 0.4);
}

.header-nav a:hover,
.header-nav a:focus {
  color: #ffffff !important;
}

.header-nav li {
  display: inline-block;
  padding-left: 0;
  margin: 0 1rem;
}

/* ------------------------------------------------------------------- 
 * ## mobile menu toggle
 * ------------------------------------------------------------------- */
.header-menu-toggle {
  z-index: 101;
  display: none;
  height: 4.2rem;
  width: 4.2rem;
  line-height: 4.2rem;
  font-family: "Gothic A1", sans-serif;
  font-size: 1.4rem;
  text-transform: uppercase;
  letter-spacing: .2rem;
  color: #ffffff;
  outline: none;
  transition: all .3s;
  position: absolute;
  right: 5.2rem;
  top: 1.6rem;
}

.header-menu-toggle:hover,
.header-menu-toggle:focus {
  color: #ffffff;
}

.header-menu-toggle span {
  display: block;
  width: 2.4rem;
  height: 2px;
  background-color: #ffffff;
  transition: all .5s;
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  margin-top: -1px;
  position: absolute;
  top: 50%;
  left: .9rem;
  right: auto;
  bottom: auto;
}

.header-menu-toggle span::before,
.header-menu-toggle span::after {
  content: '';
  width: 100%;
  height: 100%;
  background-color: inherit;
  transition: all .5s;
  position: absolute;
  left: 0;
}

.header-menu-toggle span::before {
  top: -.9rem;
}

.header-menu-toggle span::after {
  bottom: -.9rem;
}

.header-menu-toggle.is-clicked span {
  background-color: rgba(255, 255, 255, 0);
  transition: all .1s;
}

.header-menu-toggle.is-clicked span::before,
.header-menu-toggle.is-clicked span::after {
  background-color: white;
}

.header-menu-toggle.is-clicked span::before {
  top: 0;
  transform: rotate(135deg);
}

.header-menu-toggle.is-clicked span::after {
  bottom: 0;
  transform: rotate(225deg);
}

/* ------------------------------------------------------------------- 
 * ## download resume button
 * ------------------------------------------------------------------- */
.header-content .btn {
  border-color: #ffffff;
  color: #ffffff;
  margin: 0;
  padding: 0 4rem;
  font-size: 1.1rem;
  transform: translate3d(0, -50%, 0);
  position: absolute;
  top: 50%;
  right: 8rem;
}

.header-content .btn:hover,
.header-content .btn:focus {
  background-color: #03E39B !important;
  border-color: #03E39B !important;
}

/* ------------------------------------------------------------------- 
 * responsive:
 * header
 * ------------------------------------------------------------------- */
@media screen and (max-width:1700px) {
  .header-logo {
    left: 6rem;
  }

  .header-nav-wrap {
    max-width: none;
    width: 100%;
    padding-left: 13.2rem;
  }

  .header-content .btn {
    right: 6rem;
  }

}

@media screen and (max-width:1000px) {
  .header-content .btn {
    height: 4.8rem !important;
    line-height: 4.4rem !important;
  }

}

@media screen and (max-width:900px) {
  .s-header {
    top: 2rem;
    height: 8rem;
  }

  .header-logo img {
    width: 37px;
    height: 40px;
  }

  .header-content {
    display: block;
    background-color: #050505;
    height: auto;
    width: 100%;
    padding: 16rem 6rem 8rem;
    transform: translate3d(0, -100%, 0);
    position: absolute;
    top: -2rem;
    left: 0;
  }

  .header-content .btn {
    margin-top: 4.8rem;
    transform: translate3d(0, -2rem, 0);
    opacity: 0;
    visibility: hidden;
    position: static;
  }

  .header-nav-wrap {
    padding: 0;
    transform: translate3d(0, -2rem, 0);
    opacity: 0;
    visibility: hidden;
  }

  .header-nav {
    display: block;
    width: 100%;
    padding: 0;
  }

  .header-nav a {
    display: block;
  }

  .header-nav li {
    border-top: 1px solid rgba(255, 255, 255, 0.06);
    display: block;
    margin: 0;
  }

  .header-nav li:last-child {
    border-bottom: 1px solid rgba(255, 255, 255, 0.06);
  }

  .header-menu-toggle {
    display: block;
  }

  .menu-is-open .header-content {
    transform: translate3d(0, 0, 0);
    transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  .menu-is-open .header-content .btn {
    transform: translate3d(0, 0, 0);
    opacity: 1;
    visibility: visible;
    transition-property: transform, opacity;
    transition-duration: .5s;
    transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    transition-delay: .7s;
  }

  .menu-is-open .header-nav-wrap {
    transform: translate3d(0, 0, 0);
    opacity: 1;
    visibility: visible;
    transition: all 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
    transition-delay: .6s;
  }

}

@media screen and (max-width:600px) {
  .header-logo {
    left: 4rem;
  }

  .header-menu-toggle {
    right: 3.2rem;
  }

  .header-content {
    padding: 16rem 4rem 8rem;
  }

  .header-content .btn {
    display: block;
  }

}


/* ===================================================================
 * # testimonials
 *
 * ------------------------------------------------------------------- */
.s-testimonials {
  padding-top: 14.4rem;
  padding-bottom: 16rem;
  background-color: #ffffff;
}

.testimonial-slider {
  position: relative;
}

.testimonial-slider p {
  font-family: "Gothic A1", sans-serif;
  font-weight: 400;
  font-size: 1.7rem;
  line-height: 1.882;
  color: #000000;
}

.testimonial-slider .slick-slide {
  outline: none;
}

.testimonial-slider .slick-dots {
  display: block;
  list-style: none;
  padding: 0;
  margin: 5.6rem 0 0 0;
  text-align: center;
}

.testimonial-slider .slick-dots li {
  display: inline-block;
  width: 27px;
  height: 27px;
  margin: 0;
  padding: 9px;
  cursor: pointer;
}

.testimonial-slider .slick-dots li button {
  display: block;
  width: 10px;
  height: 10px;
  line-height: 10px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.4);
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
}

.testimonial-slider .slick-dots li button:hover,
.testimonial-slider .slick-dots li button:focus {
  outline: none;
}

.testimonial-slider .slick-dots li.slick-active button,
.testimonial-slider .slick-dots li:hover button {
  background: #000000;
}

.testimonial-slider__slide {
  position: relative;
  padding: 0 20px;
}

.testimonial-slider__author {
  min-height: 7.2rem;
  margin-top: 4rem;
  padding-left: 8.8rem;
  position: relative;
}

.testimonial-slider__avatar {
  width: 6.8rem;
  height: 6.8rem;
  border-radius: 100%;
  outline: none;
  position: absolute;
  top: 0;
  left: 0;
}

.testimonial-slider__cite {
  display: block;
}

.testimonial-slider__cite strong,
.testimonial-slider__cite span {
  font-family: "Gothic A1", sans-serif;
  font-style: normal;
}

.testimonial-slider__cite strong {
  font-size: 1.6rem;
  line-height: 1.75;
}

.testimonial-slider__cite span {
  display: block;
  font-size: 1.5rem;
  line-height: 1.333;
  color: rgba(0, 0, 0, 0.7);
}

/* ------------------------------------------------------------------- 
 * responsive:
 * testimonials
 * ------------------------------------------------------------------- */
@media screen and (max-width:800px) {
  .testimonial-slider__slide {
    text-align: center;
  }

  .testimonial-slider__author {
    display: inline-block;
  }

}

@media screen and (max-width:400px) {
  .testimonial-slider__author {
    margin: 0;
    padding: 0;
  }

  .testimonial-slider__avatar {
    position: static;
    margin-bottom: 2.4rem;
  }

}


